import React from "react";
import pp from '../images/pp - 250 X 250.jpg'
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BsFillEnvelopeFill } from "react-icons/bs";
import { BsLinkedin } from "react-icons/bs";

var name = "Ejaj Ahmed";
var title = "Game Developer"
var web_url = "https://getbootstrap.com/docs/4.0/content/images/"

export default function Header(){
    return(
        <div className="header">
            <Image />
            <Info />
            <Buttons />
        </div>
    );
}

function Image(){
    return(
        <img src={pp} />
    );
}

function Info(){
    return (
        <div id="info">
            <h1>{name}</h1>
            <h3>{title}</h3>
            <h6>{web_url}</h6>
        </div>
    );
}

function Buttons(){
    return (
        <div id="buttons">
            <Button variant="secondary" href="mailto:iahmed1400@gmail.com" target="_blank">
                <BsFillEnvelopeFill />
                Email
            </Button>
            <Button variant="primary" href="https://www.linkedin.com/in/iahmed1400/" target="_blank">
                <BsLinkedin />
                LinkedIn
            </Button>
        </div>
    );
}